import React from 'react';
import styled from 'styled-components';
import { Mouse, Eyes, Bacteria, BacteriaPickedPartType } from 'App/datas';
import { scaleUp } from 'styles/animations';

import SquintEye from 'components/bacteriaParts/eyes/Squint';
import EmptyEye from 'components/bacteriaParts/eyes/Empty';
import BeanEye from 'components/bacteriaParts/eyes/Bean';
import WinkEye from 'components/bacteriaParts/eyes/Wink';
import HeartEye from 'components/bacteriaParts/eyes/Heart';
import CatEye from 'components/bacteriaParts/eyes/Cat';
import FrankEye from 'components/bacteriaParts/eyes/Frank';
import AngryEye from 'components/bacteriaParts/eyes/Angry';
import SmileEye from 'components/bacteriaParts/eyes/Smile';

import NormalMouse from 'components/bacteriaParts/mouses/Normal';
import SmileMouse from 'components/bacteriaParts/mouses/Smile';
import SquareTeethMouse from 'components/bacteriaParts/mouses/SquareTeeth';
import TongueSmileMouse from 'components/bacteriaParts/mouses/TongueSmile';
import BloodMouse from 'components/bacteriaParts/mouses/Blood';
import PuppyMouse from 'components/bacteriaParts/mouses/Puppy';
import SalivaMouse from 'components/bacteriaParts/mouses/Saliva';
import SurprisedMouse from 'components/bacteriaParts/mouses/Surprised';
import ThreeTeethMouse from 'components/bacteriaParts/mouses/ThreeTeeth';
import Bodies from './bacteriaParts/bodies';

const eyesMap = {
  [Eyes.Good]: [HeartEye, WinkEye, SmileEye],
  [Eyes.Soso]: [SquintEye, EmptyEye, BeanEye],
  [Eyes.Bad]: [AngryEye, FrankEye, CatEye],
};

const renderEyes = (eyes: Eyes, pickedEyes?: number) => {
  const eyesGroup = eyesMap[eyes];
  const randomNum =
    pickedEyes === undefined
      ? Math.floor(Math.random() * eyesGroup.length)
      : pickedEyes;
  const EyesComponent = eyesGroup[randomNum];
  return <EyesComponent />;
};

const mouseMap = {
  [Mouse.Good]: [PuppyMouse, TongueSmileMouse, SmileMouse],
  [Mouse.Soso]: [SquareTeethMouse, SurprisedMouse, NormalMouse],
  [Mouse.Bad]: [BloodMouse, ThreeTeethMouse, SalivaMouse],
};

const renderMouse = (mouse: Mouse, pickedMouse?: number) => {
  const mouseGroup = mouseMap[mouse];
  const randomNum =
    pickedMouse === undefined
      ? Math.floor(Math.random() * mouseGroup.length)
      : pickedMouse;
  const MouseComponent = mouseGroup[randomNum];
  return <MouseComponent />;
};

const BodyDiv = styled.div`
  position: relative;
  animation: ${scaleUp} 0.7s infinite alternate;
`;
const EyeDiv = styled.div`
  position: absolute;
  top: 15%;
  width: 100%;
  height: 27.5%;
`;
const MouseDiv = styled.div`
  width: 100%;
  height: 25%;
  position: absolute;
  top: 42.5%;
`;

type Props = {
  type: Bacteria | null;
  detail?: BacteriaPickedPartType;
  size?: number;
};

const BacteriaCombination: React.FC<Props> = ({
  type,
  detail = {
    pickedBody: undefined,
    pickedEyes: undefined,
    pickedMouse: undefined,
    pickedColor: undefined,
  },
  size = 19,
}) => {
  if (!type) {
    return <div>-</div>;
  }
  const { body, eyes, mouse, color } = type;
  const { pickedBody, pickedEyes, pickedMouse, pickedColor } = detail;

  return (
    <BodyDiv
      style={{
        width: `${size}rem`,
        height: `${size}rem`,
        fontSize: `${size}px`,
      }}
    >
      <Bodies
        body={body}
        color={color}
        pickedBody={pickedBody}
        pickedColor={pickedColor}
      />
      <EyeDiv>{renderEyes(eyes, pickedEyes)}</EyeDiv>
      <MouseDiv>{renderMouse(mouse, pickedMouse)}</MouseDiv>
    </BodyDiv>
  );
};
export default React.memo(BacteriaCombination);
