import React from 'react';
import styled from 'styled-components';

import NoBacteriaGroupA_0 from 'assets/images/seed.png';
import NoBacteriaGroupA_1 from 'assets/images/dandelion.png';
import NoBacteriaGroupA_2 from 'assets/images/pine-cone.png';

import NoBacteriaGroupB_0 from 'assets/images/sesame.png';
import NoBacteriaGroupB_1 from 'assets/images/rope.png';
import NoBacteriaGroupB_2 from 'assets/images/candy.png';

import NoBacteriaGroupC_0 from 'assets/images/jupiter.png';
import NoBacteriaGroupC_1 from 'assets/images/milky-way.png';
import NoBacteriaGroupC_2 from 'assets/images/dna.png';

import { scaleUp } from 'styles/animations';

const somethingRender = (type: string) => {
  switch (type) {
    case 'noBacteriaGroupA':
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <img src={NoBacteriaGroupA_0} alt="" />;
        case 1:
          return <img src={NoBacteriaGroupA_1} alt="" />;
        case 2:
          return <img src={NoBacteriaGroupA_2} alt="" />;
        default:
          break;
      }
      return <></>;
    default:
      break;

    case 'noBacteriaGroupB':
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <img src={NoBacteriaGroupB_0} alt="" />;
        case 1:
          return <img src={NoBacteriaGroupB_1} alt="" />;
        case 2:
          return <img src={NoBacteriaGroupB_2} alt="" />;
        default:
          break;
      }
      return <></>;

    case 'noBacteriaGroupC':
      switch (Math.floor(Math.random() * 3)) {
        case 0:
          return <img src={NoBacteriaGroupC_0} alt="" />;
        case 1:
          return <img src={NoBacteriaGroupC_1} alt="" />;
        case 2:
          return <img src={NoBacteriaGroupC_2} alt="" />;
        default:
          break;
      }
      return <></>;
  }
  return <></>;
};

type Props = {
  type: string;
  size?: number;
};

type ImgProps = {
  size?: number;
};

const SometingDiv = styled.div<ImgProps>`
  display: flex;
  justify-content: center;
  align-items: center;

  animation: ${scaleUp} 0.7s infinite alternate;

  img {
    width: ${(props) => props.size}rem;
    z-index: 9;
  }
`;

const RenderSomething: React.FC<Props> = ({ type, size = 14 }) => {
  return <SometingDiv size={size}>{somethingRender(type)}</SometingDiv>;
};
export default React.memo(RenderSomething);
