import React, { useContext } from 'react';
import AppContext from 'App/context';
import styled from 'styled-components';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import { CustomBtn, CustomBtnDiv } from 'components/EtcStyles';
import AirFresh from 'assets/images/airfresh.gif';

const Div = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
`;

const Page: React.FC = () => {
  const { goToNext } = useContext(AppContext);
  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>체험으로 배우는 AI 학습</ContentTitle>
          <ExplainBox>
            <div>인공지능(AI)은 사람처럼 학습을 통해 만들어져. </div>
            <div>
              가상의 <PointText>세균 탐지 AI를 만드는 체험</PointText>을 해보며,
              AI를 알아보자!
            </div>
          </ExplainBox>
          <Content>
            <Div>
              <img src={AirFresh} alt="" />
            </Div>
          </Content>
        </DivBase>
        <CustomBtnDiv>
          <CustomBtn
            onClick={() => {
              goToNext();
            }}
          >
            체험시작
          </CustomBtn>
        </CustomBtnDiv>
      </Container>
    </BasePage>
  );
};

export default Page;
