import AppContext from 'App/context';
import { PatternSecondType } from 'App/datas';
import { BacteriaTargetType } from 'pages/AIPerformanceResult2';
import React, { useContext } from 'react';
import styled, { css } from 'styled-components';
import BacteriaCombination from './BacteriaCombination';

const Container = styled.div`
  background: rgba(59, 59, 59, 0.9);
  color: white;
  padding: 2rem;
  text-align: center;
  position: absolute;
  width: 40vw;
  height: fit-content;
  font-size: 1.125rem;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 2rem;
`;

const StackedBarChartContainer = styled.div`
  width: 100%;

  & > div {
    display: flex;
    width: 100%;
    height: 2em;
    justify-content: space-between;
  }

  & > div + div {
    margin-top: 0.5rem;
  }
`;

type ChartDataType = {
  color: string;
  value: number;
  label: string;
};

const Bar = styled.div<Omit<ChartDataType, 'label'>>`
  ${({ color, value }) => css`
    background: ${color};
    width: ${value}%;
  `}
`;

const Label = styled.div<Pick<ChartDataType, 'color'>>`
  ${({ color }) => css`
    color: ${color};
  `}
`;

const StackedBarChart: React.FC<{ datas: ChartDataType[] }> = ({ datas }) => {
  return (
    <StackedBarChartContainer>
      <div>
        {datas.filter(({ value }) => Number.isNaN(value)).length > 0 ? (
          <Bar color="gray" value={100} />
        ) : (
          datas.map(({ color, value, label }) => (
            <Bar key={label} color={color} value={value} />
          ))
        )}
      </div>
      <div>
        {datas.map(({ color, label, value }) => (
          <Label key={label} color={color}>
            {`${label} ${Number.isNaN(value) ? '?' : value}%`}
          </Label>
        ))}
      </div>
    </StackedBarChartContainer>
  );
};

type PatternSecondTypeKey = keyof PatternSecondType;

const patternSecondKeys = [
  'body',
  'color',
  'mouse',
  'eyes',
] as Array<PatternSecondTypeKey>;

const patternSecondNames: Record<PatternSecondTypeKey, string> = {
  body: '몸통',
  color: '색깔',
  eyes: '눈',
  mouse: '입',
};

const BacteriaChart: React.FC<{ target: BacteriaTargetType }> = ({
  target: { bacteria, detail },
}) => {
  const {
    state: {
      modelsSecond: { noxious, innoxious },
    },
  } = useContext(AppContext);
  const scoreNoxious =
    patternSecondKeys.reduce(
      (prev, current) => prev + noxious.pattern[current][bacteria[current]],
      0,
    ) / (noxious.learnCount || 1);
  const scoreInnoxious =
    patternSecondKeys.reduce(
      (prev, current) => prev + innoxious.pattern[current][bacteria[current]],
      0,
    ) / (innoxious.learnCount || 1);
  const total = scoreNoxious + scoreInnoxious;
  const percentNoxious = Math.round((100 * scoreNoxious) / total);
  const percentInnoxious = 100 - percentNoxious;
  return (
    <Container>
      <h1>AI 예측 결과</h1>
      <StackedBarChart
        datas={[
          {
            color: '#60C492',
            label: '무해균',
            value: percentInnoxious,
          },
          {
            color: '#FD586A',
            label: '유해균',
            value: percentNoxious,
          },
        ]}
      />
      <h3>세균을 구분하는 세부 기준에 대한 예측결과</h3>
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-evenly',
          alignItems: 'center',
        }}
      >
        <BacteriaCombination type={bacteria} detail={detail} size={10} />
        <div style={{ width: '75%' }}>
          {patternSecondKeys.map((part) => {
            const key = bacteria[part];
            const scoreNoxiousPart =
              noxious.pattern[part][key] / (noxious.learnCount || 1);
            const scoreInnoxiousPart =
              innoxious.pattern[part][key] / (innoxious.learnCount || 1);
            const totalPart = scoreNoxiousPart + scoreInnoxiousPart;
            const percentNoxiousPart = Math.round(
              (100 * scoreNoxiousPart) / totalPart,
            );
            const percentInnoxiousPart = 100 - percentNoxiousPart;
            return (
              <div
                key={part}
                style={{
                  display: 'flex',
                  marginTop: '0.5rem',
                  fontSize: '0.875rem',
                }}
              >
                <span style={{ width: '4em', padding: '0.5em' }}>
                  {patternSecondNames[part]}
                </span>
                <StackedBarChart
                  datas={[
                    {
                      color: '#60C492',
                      label: '무해균',
                      value: percentInnoxiousPart,
                    },
                    {
                      color: '#FD586A',
                      label: '유해균',
                      value: percentNoxiousPart,
                    },
                  ]}
                />
              </div>
            );
          })}
        </div>
      </div>
    </Container>
  );
};

export default BacteriaChart;
