import React, { useContext, useMemo } from 'react';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import {
  FlexDiv,
  LearnPageDiv,
  RightBtnDiv,
  MicroImg,
  LearnPageBtn,
  MicroCircle,
  Trapezoid,
  OXBtnDiv,
  BatteryDiv,
  TrashBtn,
  TrashDiv,
  CustomPopover,
} from 'components/EtcStyles';
import BacteriaCombination from 'components/BacteriaCombination';
import AppContext from 'App/context';
import {
  PatternFirstType,
  generateBacteria,
  generateModelFirstLabel,
  modelFirst,
} from 'App/datas';
import RenderNoBacteria from 'components/RenderNoBacteria';
import Micro from 'assets/images/micro.png';
import { FaRegTrashAlt as Trash } from 'react-icons/fa';
import HelpBoxByGage from 'components/HelpBoxByGage';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';

const Page: React.FC = () => {
  const {
    state: { modelsFirst },
    setState,
    goToNext,
    learnBacteria,
    learnNoBacteria,
  } = useContext(AppContext);

  const gage =
    modelsFirst.bacteria.learnCount + modelsFirst.noBacteria.learnCount;
  const zeroPaddedGage = ['0', gage.toString()].join('').slice(-2);
  const handleTarget = () => {
    const param = generateModelFirstLabel();
    return {
      param,
      bacteria: param === 'bacteria' ? generateBacteria() : null,
    };
  };
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const target = useMemo(handleTarget, [gage]);
  const { param, bacteria } = target;

  const handleChange = (learn: (_: keyof PatternFirstType) => void) => () => {
    if (gage < 50) {
      learn(param);
    }
  };
  const handleChangeO = handleChange(learnBacteria);
  const handleChangeX = handleChange(learnNoBacteria);

  const [open, setOpen] = React.useState(true);
  const handleTooltipClose = () => {
    setOpen(false);
  };

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>1. AI 학습 시키기</ContentTitle>
          <ExplainBox>
            AI는 처음에 공기 중 물체가 세균인지, 아닌지 구분할 수 없어.
            <div>
              <PointText>AI는 우리가 알려준대로 물체를 인식</PointText>하게
              될거야! 이제 AI에게 세균을 알려주자!
            </div>
          </ExplainBox>
          <Content style={{ height: 'calc(100% - 5rem)' }}>
            <FlexDiv>
              <LearnPageDiv>
                <MicroImg src={Micro} alt="" />
                <Trapezoid />
                <MicroCircle>
                  <ClickAwayListener onClickAway={handleTooltipClose}>
                    <BatteryDiv>
                      <CustomPopover isOpen={open}>
                        처음부터 다시 시작하려면 여길 눌러 학습데이터를 비워봐!
                      </CustomPopover>
                      <TrashDiv>
                        <TrashBtn
                          onClick={() =>
                            setState((prevState) => ({
                              ...prevState,
                              modelsFirst: {
                                bacteria: { ...modelFirst },
                                noBacteria: { ...modelFirst },
                              },
                            }))
                          }
                        >
                          <Trash />
                        </TrashBtn>
                        {zeroPaddedGage} / 50
                      </TrashDiv>
                    </BatteryDiv>
                  </ClickAwayListener>
                  {param === 'bacteria' ? (
                    <BacteriaCombination type={bacteria} />
                  ) : (
                    <RenderNoBacteria type={param} />
                  )}
                </MicroCircle>
                <OXBtnDiv>
                  <LearnPageBtn onClick={handleChangeO} color="#FF5667">
                    세균이다
                  </LearnPageBtn>
                  <LearnPageBtn onClick={handleChangeX} color="#5BC590">
                    세균아님
                  </LearnPageBtn>
                </OXBtnDiv>
                <RightBtnDiv>
                  <LearnPageBtn
                    onClick={() => {
                      goToNext();
                    }}
                    disabled={gage < 5}
                    color="#1696e5"
                    style={{ margin: 0 }}
                  >
                    학습완료
                  </LearnPageBtn>
                </RightBtnDiv>
              </LearnPageDiv>
            </FlexDiv>
            <HelpBoxByGage gage={gage} />
          </Content>
        </DivBase>
      </Container>
    </BasePage>
  );
};

export default Page;
