import React, { useContext, useMemo } from 'react';
import AppContext from 'App/context';
import BasePage from 'components/BasePage';
import {
  Container,
  DivBase,
  ContentTitle,
  Content,
  ExplainBox,
  PointText,
} from 'components/BaseStyles';
import {
  FlexDiv,
  RightBtnDiv,
  LearnPageDiv,
  MicroImg,
  MicroCircle,
  Trapezoid,
  BatteryDiv,
  LearnPageBtn,
  OXBtnDiv,
  TrashBtn,
  TrashDiv,
  CustomPopover,
} from 'components/EtcStyles';
import Combination from 'components/BacteriaCombination';
import { Bacteria, generateBacteria, modelSecond } from 'App/datas';
import Micro from 'assets/images/micro.png';
import { FaRegTrashAlt as Trash } from 'react-icons/fa';
import HelpBoxByGage from 'components/HelpBoxByGage';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';

const Page: React.FC = () => {
  const {
    state: { modelsSecond },
    setState,
    goToNext,
    learnNoxiousBacteria,
    learnInnoxiousBacteria,
  } = useContext(AppContext);

  const gage =
    modelsSecond.noxious.learnCount + modelsSecond.innoxious.learnCount;
  const zeroPaddedGage = ['0', gage.toString()].join('').slice(-2);

  const bacteria = useMemo(generateBacteria, [gage]);

  const handleChange = (learn: (_: Bacteria) => void) => () => {
    if (gage < 50) {
      learn(bacteria);
    }
  };
  const handleChangeO = handleChange(learnNoxiousBacteria);
  const handleChangeX = handleChange(learnInnoxiousBacteria);

  const [open, setOpen] = React.useState(true);
  const handleTooltipClose = () => {
    setOpen(false);
  };

  return (
    <BasePage>
      <Container>
        <DivBase>
          <ContentTitle>1. AI 학습 시키기</ContentTitle>
          <ExplainBox>
            자 이제 세균 탐지 AI를 위한 학습을 새로 해보자. AI는 우리가
            알려준대로 세균을 구분하게 될거야!
            <div>
              세균의 모양, 색깔, 표정 등을 고려하여
              <PointText> 나의 판단기준을 AI에게 알려주자.</PointText>
            </div>
          </ExplainBox>
          <Content style={{ height: 'calc(100% - 5rem)' }}>
            <FlexDiv>
              <LearnPageDiv>
                <MicroImg src={Micro} alt="" />
                <Trapezoid />
                <MicroCircle>
                  <ClickAwayListener onClickAway={handleTooltipClose}>
                    <BatteryDiv>
                      <CustomPopover isOpen={open}>
                        처음부터 다시 시작하려면 여길 눌러 학습데이터를 비워봐!
                      </CustomPopover>
                      <TrashDiv>
                        <TrashBtn
                          onClick={() =>
                            setState((prevState) => ({
                              ...prevState,
                              modelsSecond: {
                                noxious: { ...modelSecond },
                                innoxious: { ...modelSecond },
                              },
                            }))
                          }
                        >
                          <Trash />
                        </TrashBtn>
                        {zeroPaddedGage} / 50
                      </TrashDiv>
                    </BatteryDiv>
                  </ClickAwayListener>
                  <Combination type={bacteria} />
                </MicroCircle>
                <OXBtnDiv>
                  <LearnPageBtn onClick={handleChangeX} color="#5BC590">
                    무해균
                  </LearnPageBtn>
                  <LearnPageBtn onClick={handleChangeO} color="#FF5667">
                    유해균
                  </LearnPageBtn>
                </OXBtnDiv>
                <RightBtnDiv>
                  <LearnPageBtn
                    onClick={goToNext}
                    disabled={gage < 5}
                    color="#1696e5"
                    style={{ margin: 0 }}
                  >
                    학습완료
                  </LearnPageBtn>
                </RightBtnDiv>
              </LearnPageDiv>
            </FlexDiv>
            <HelpBoxByGage gage={gage} />
          </Content>
        </DivBase>
      </Container>
    </BasePage>
  );
};

export default React.memo(Page);
