import React from 'react';
import { Color, Body } from 'App/datas';
import Cloud from './Cloud';
import Diamond from './Diamond';
import Nail from './Nail';
import Round from './Round';
import Square from './Square';
import Star from './Star';

const colorMap = {
  [Color.GroupRed]: ['#ecbaba', '#b28383'],
  [Color.GroupYellow]: ['#f0e4a7', '#cdc89a'],
  [Color.GroupGreen]: ['#c8e1c2', '#adbda9'],
  [Color.GroupBlue]: ['#bfcbe7', '#a9b3c9'],
};

const renderColor = (color: Color, pickedColor?: number) => {
  const colorGroup = colorMap[color];
  const randomNum =
    pickedColor === undefined
      ? Math.floor(Math.random() * colorGroup.length)
      : pickedColor;
  return colorGroup[randomNum];
};

const bodyMap = {
  [Body.Good]: [Round, Cloud],
  [Body.Soso]: [Nail, Square],
  [Body.Bad]: [Star, Diamond],
};

const renderBody = (body: Body, color: string, pickedBody?: number) => {
  const bodyGroup = bodyMap[body];
  const randomNum =
    pickedBody === undefined
      ? Math.floor(Math.random() * bodyGroup.length)
      : pickedBody;
  const BodyComponent = bodyGroup[randomNum];
  return <BodyComponent color={color} />;
};

const Bodies: React.FC<{
  color: Color;
  body: Body;
  pickedColor?: number;
  pickedBody?: number;
}> = ({ body, color, pickedBody, pickedColor }) => {
  const bodyColor = renderColor(color, pickedColor);
  return renderBody(body, bodyColor, pickedBody);
};

export default Bodies;
