import React, { useContext } from 'react';
import styled from 'styled-components';
import { learningPage1Index, learningPage2Index } from 'App';
import AppContext from 'App/context';

const Div = styled.div`
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  background: #ececec;
  padding: 1rem;
  border-radius: 1rem;
  max-width: 32rem;
  z-index: 9;
  box-shadow: 5px 5px 7px #c4c4c4;
  font-size: 0.9rem;
`;
const BoldTitle = styled.div`
  font-weight: bold;
  font-size: 1.1rem;
  margin-bottom: 1rem;
`;
const RestartBtn = styled.button`
  width: 45%;
  border: 1px solid #1696e5;
  border-radius: 0.5rem;
  background-color: #fff;
  cursor: pointer;
  font-family: 'Yspot';
  color: #1696e5;
  font-size: 1rem;
  padding: 0.5rem;
  margin-top: 1rem;
  :hover {
    color: #535353;
  }
`;

type Props = {
  goTo: string;
};

const PlayAgainBox: React.FC<Props> = ({ goTo }) => {
  const { setState } = useContext(AppContext);
  const handleClick = () => {
    setState((prev) => ({
      ...prev,
      page: goTo === 'first-step' ? learningPage1Index : learningPage2Index,
    }));
  };

  return (
    <Div>
      {goTo === 'first-step' ? (
        <>
          <BoldTitle>AI가 세균을 잘 구분 못하나요? </BoldTitle>
          학습 데이터를 더 제공할 수록 AI가 더 많이 배웁니다.
          <br />
          AI를 다시 학습시켜볼까요?
          <br />
        </>
      ) : (
        <>
          <BoldTitle>AI 예측결과가 나의 예상과 다르나요? </BoldTitle>
          무해균과 유해균을 구분한 나의 기준을 분명하게 정해서
          <br />
          AI를 다시 학습시켜 볼까요?
          <br />
        </>
      )}

      <RestartBtn onClick={handleClick}>다시 학습하기</RestartBtn>
    </Div>
  );
};

export default PlayAgainBox;
